}

.auto-save-indicator {
  background: #e3f2fd;
  color: #1976d2;
  padding: 10px 15px;
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
  border: 1px solid #bbdefb;
}

.auto-save-indicator i {
  color: #1976d2;
  font-size: 0.9rem;
}

.form-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 30px;
}

.form-actions .btn {
  padding: 15px 30px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  border: none;
