}

/* Responsive Design */
@media (max-width: 768px) {
  .create-job-page {
    padding: 0 15px 15px 15px;
  }
  
  .page-header {
    padding: 20px;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    top: 0;
    border-radius: 0 0 15px 15px;
