            height: 4px;
            background: linear-gradient(90deg, #6f42c1, #5a32a3);
        }

        .offer-item:hover {
            background: rgba(255, 255, 255, 0.08);
            transform: translateY(-2px);
            box-shadow: 0 4px 15px rgba(111, 66, 193, 0.1);
            border-color: rgba(111, 66, 193, 0.3);
        }

        .offer-header {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            margin-bottom: 12px;
        }

        .offer-title-section {
            flex: 1;
        }

        .offer-title {
            font-size: 1.2rem;
            font-weight: 600;
            color: var(--text-primary);
            margin: 0 0 4px 0;
        }

        .offer-company {
            font-size: 1rem;
            color: var(--text-secondary);
            font-weight: 500;
            display: block;
            margin-bottom: 4px;
        }

        .recruiter-info {
            font-size: 0.85rem;
            color: var(--text-muted);
            display: flex;
            align-items: center;
            gap: 4px;
        }

        .recruiter-info i {
            color: #6f42c1;
        }

        .offer-status {
            display: flex;
            align-items: center;
            gap: 6px;
            padding: 6px 12px;
            border-radius: 20px;
            font-size: 0.8rem;
            font-weight: 500;
            text-transform: uppercase;
            letter-spacing: 0.5px;
        }

        .offer-status.pending {
            background: rgba(255, 193, 7, 0.2);
            color: #ffc107;
            border: 1px solid rgba(255, 193, 7, 0.3);
        }

        .offer-status.accepted {
            background: rgba(40, 167, 69, 0.2);
            color: #28a745;
            border: 1px solid rgba(40, 167, 69, 0.3);
        }

        .offer-status.declined {
            background: rgba(220, 53, 69, 0.2);
            color: #dc3545;
            border: 1px solid rgba(220, 53, 69, 0.3);
        }

        .offer-details {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
            gap: 12px;
            margin-bottom: 16px;
        }

        .offer-detail {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 0.9rem;
            color: var(--text-secondary);
        }

        .offer-detail i {
            color: #6f42c1;
            width: 16px;
            text-align: center;
        }

        .recruiter-message {
            background: rgba(111, 66, 193, 0.1);
            border-left: 4px solid #6f42c1;
            padding: 16px;
            margin-bottom: 16px;
            border-radius: 0 8px 8px 0;
        }

        .message-header {
            display: flex;
            align-items: center;
            gap: 8px;
            margin-bottom: 8px;
            font-weight: 500;
            color: var(--text-primary);
            font-size: 0.9rem;
        }

        .message-header i {
            color: #6f42c1;
        }

        .message-text {
            margin: 0;
            color: var(--text-secondary);
            font-style: italic;
            line-height: 1.5;
            font-size: 0.9rem;
        }

        .job-description,
        .job-requirements {
            margin-bottom: 16px;
        }

        .job-description h4,
        .job-requirements h4 {
            margin: 0 0 8px 0;
            color: var(--text-primary);
            font-size: 1rem;
            font-weight: 600;
        }

        .job-description p,
        .job-requirements p {
            margin: 0;
            color: var(--text-secondary);
            line-height: 1.6;
            font-size: 0.9rem;
        }

        .offer-progress {
            display: flex;
            flex-direction: column;
            gap: 6px;
            margin-bottom: 16px;
        }

        .progress-bar {
            width: 100%;
            height: 6px;
            background: rgba(111, 66, 193, 0.1);
            border-radius: 3px;
            overflow: hidden;
        }

        .progress-fill {
            height: 100%;
            background: linear-gradient(90deg, #6f42c1, #5a32a3);
            border-radius: 3px;
            transition: width 0.3s ease;
        }

        .progress-text {
            font-size: 0.85rem;
            color: var(--text-secondary);
            font-weight: 500;
        }

        .offer-actions {
            display: flex;
            gap: 12px;
            flex-wrap: wrap;
        }

        .offer-actions .btn {
            flex: 1;
            min-width: 120px;
            font-size: 0.9rem;
            padding: 8px 16px;
        }

        .offer-actions .btn-success {
            background: var(--success-color);
            border-color: var(--success-color);
        }

        .offer-actions .btn-success:hover {
            background: #218838;
            border-color: #1e7e34;
        }

        .offer-actions .btn-danger {
            background: var(--danger-color);
            border-color: var(--danger-color);
        }

        .offer-actions .btn-danger:hover {
            background: #c82333;
            border-color: #bd2130;
        }

        .offer-actions .btn-secondary {
            background: var(--text-accent);
            border-color: var(--text-accent);
        }

        .offer-actions .btn-secondary:hover {
            background: #0056b3;
            border-color: #004085;
        }

        @media (max-width: 768px) {
            .offer-header {
                flex-direction: column;
                gap: 8px;
            }
            
            .offer-details {
                grid-template-columns: 1fr;
            }
            
            .offer-actions {
                flex-direction: column;
            }
            
            .offer-actions .btn {
                flex: none;
                width: 100%;
            }
        }

        .profile-card,
        .section-card,
        .job-item,
        .project-item {
            will-change: transform;
            transform: translateZ(0);
        }

        .main-content {
            contain: layout style;
        }

        .profile-card {
            contain: layout style paint;
        }

        .section-card {
            contain: layout style;
        }

        @media (prefers-reduced-motion: no-preference) {
            .profile-card:hover,
            .section-card:hover,
            .job-item:hover,
            .project-item:hover {
                will-change: transform;
            }
        }

        img[loading="lazy"] {
            opacity: 0;
            transition: opacity 0.3s ease;
        }

        img[loading="lazy"].loaded {
            opacity: 1;
        }

        .header,
        .profile-card {
            contain: layout style paint;
        }

        @font-face {
            font-family: 'Segoe UI';
            font-display: swap;
            src: local('Segoe UI');
        }

        .nav-item,
        .edit-btn,
        .btn {
            contain: layout style;
        }

        .ai-score-simple {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 20px 0;
        }

        .ai-score-number {
            font-size: 3rem;
            font-weight: 900;
            background: linear-gradient(135deg, #007bff, #0056b3, #004085);
            background-size: 200% 200%;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-shadow: 0 0 20px rgba(0, 123, 255, 0.5);
            line-height: 1;
            position: relative;
            animation: glowPulse 2s ease-in-out infinite alternate, gradientShift 3s ease infinite;
        }

        .ai-score-number::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: linear-gradient(135deg, #007bff, #0056b3, #004085);
            background-size: 200% 200%;
            border-radius: 50%;
            filter: blur(15px);
            opacity: 0.3;
            z-index: -1;
            animation: gradientShift 3s ease infinite;
        }


        @keyframes glowPulse {
            from {
                text-shadow: 0 0 20px rgba(0, 123, 255, 0.5);
            }
            to {
                text-shadow: 0 0 30px rgba(0, 123, 255, 0.8), 0 0 40px rgba(0, 123, 255, 0.3);
            }
        }

        @keyframes gradientShift {
            0% {
                background-position: 0% 50%;
            }
            50% {
                background-position: 100% 50%;
            }
            100% {
                background-position: 0% 50%;
            }
        }